import { NavLink } from "react-router-dom"
import styles from "./Header.module.css"
const Header=()=>{
    return(
        <div className={styles.header}>
        <div className={styles.tabs}>
          <NavLink to="/"><button className={styles.tab}></button></NavLink>
          <NavLink to="/directory"><button className={styles.tab}></button></NavLink>
          <NavLink to="/users"><button className={styles.tab}></button></NavLink>
        </div>
        
      </div>
    )
}

export default Header